import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
import { MsalService } from '@azure/msal-angular';
import { fetchMsalModuleConfigSynchronously } from '../../shared/functions/fetchMsalModuleConfigSynchronously';

export function parseJwt (token) {
  var base64Url = token.split('.')[1];
  var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
  var jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
      return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
  }).join(''));

  return JSON.parse(jsonPayload);
};

@Injectable({
  providedIn: 'root'
})
export class RomeInterceptorService implements HttpInterceptor {

  protected MsalConfig: any;

  constructor(protected msalService: MsalService,
    protected router: Router) { 
      this.MsalConfig = fetchMsalModuleConfigSynchronously();
    }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    let auth = this.msalService.getUser();

    if (req.url.indexOf('api/v1') > -1 && auth) {
      if (this.tokenHasExpired())
        this.router.navigate(['logout']);
      else {
        if (this.refreshToken())
          this.acquireTokenSilent();

        req = req.clone({
          setHeaders: {
            "Authorization": "Bearer " + localStorage["msal.idtoken"]
          }
        });

        return next.handle(req).pipe(
          catchError((error) => {
            if (error instanceof HttpErrorResponse) {
              switch (error.status) {
                case 401:
                  this.router.navigate(['unauthorised']);
                  break;
                case 403:
                case 405:
                  this.router.navigate(['forbidden']);
                  break;
                case 408:
                case 504:
                  this.router.navigate(['timeout']);
                  break;
                case 500:
                  if (error instanceof HttpErrorResponse && error.error instanceof Blob && error.error.type === "application/json") {
                    return new Promise<any>((resolve, reject) => {
                      let reader = new FileReader();
                      reader.onload = (e: Event) => {
                        const errmsg = JSON.parse((<any>e.target).result);
                        let requestId = errmsg.requestId;
                        this.router.navigate(['internal-server-error', { requestId: requestId }]);
                      };
                      reader.readAsText(error.error);
                    });
                  }
                  else {
                    let requestId = error.error.requestId;
                    this.router.navigate(['internal-server-error', { requestId: requestId }]);
                  }
                  break;
              }
            }
            return throwError(error);
          })
        );
      }
    }
    else
      return next.handle(req);
  }

  protected tokenHasExpired(): boolean {
    let dateTimeNow = new Date().getTime() / 1000;

    if (dateTimeNow <= parseJwt(this.msalService.getUser().idToken).exp) {
      return false;
    }
    else {
      return true;
    }
  }

  protected refreshToken(): boolean {
    let timeToRenewToken = new Date().getTime() + this.MsalConfig.renewTokenBeforeExpires;
    if (timeToRenewToken >= parseJwt(this.msalService.getUser().idToken).exp)
      return true;
    else
      return false;
  }

  protected acquireTokenSilent() {
    var loginHint = "login_hint=" + parseJwt(this.msalService.getUser().idToken).email;

    this.msalService.acquireTokenSilent([this.MsalConfig.clientID],
      this.MsalConfig.authority,
      this.msalService.getUser(), loginHint)
      .then(response => {
        localStorage.setItem('msal.idtoken', response);
      })
      .catch(err => {
        if (err.name === "InteractionRequiredAuthError") {
          return this.msalService.acquireTokenPopup([this.MsalConfig.clientID],
            this.MsalConfig.authority,
            this.msalService.getUser(), loginHint)
            .then(response => {
              localStorage.setItem('msal.idtoken', response);
            })
            .catch(err => {

            });
        }
      });
  }
}