import { Component, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  scrolled: boolean;

  private popupOpenSubscription: Subscription;
  private popupCloseSubscription: Subscription;
  private initializeSubscription: Subscription;
  private statusChangeSubscription: Subscription;
  private revokeChoiceSubscription: Subscription;
  private noCookieLawSubscription: Subscription;

  constructor(public router: Router,
    private titleService: Title) {
  }

  ngOnInit() {
    this.scrolled = false;
  }

  ngOnDestroy() {
    this.popupOpenSubscription.unsubscribe();
    this.popupCloseSubscription.unsubscribe();
    this.initializeSubscription.unsubscribe();
    this.statusChangeSubscription.unsubscribe();
    this.revokeChoiceSubscription.unsubscribe();
    this.noCookieLawSubscription.unsubscribe();
  }

  reOpenCookieWindow() {
  }

  @HostListener("window:scroll", [])
  onWindowScroll() {
    const scrollDistance = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
    this.scrolled = (scrollDistance > 0);
  }

  onActivate(event) {
    window.scroll(0, 0);
  }

  displayNavigation() {
    let locationContainsAnySegment: boolean;
    const segment: string[] = [
      'login',
      'signup',
      'unauthorised',
      'internal-server-error',
      'invitation-expired',
      'forgot-password',
      'privacy-statement',
      'state-specific-privacy',
      'forbidden',
      'timeout',
      'down-for-maintenance'
    ]

    segment.map((value) => {
      if (location.href.indexOf(value) > -1)
        locationContainsAnySegment = true;
    })

    return !locationContainsAnySegment;
  }
}
