import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule, HttpClient, HTTP_INTERCEPTORS } from '@angular/common/http';
import { MsalService, BroadcastService } from '@azure/msal-angular';
import { AppConfigService } from './services/app-config/app-config.service';
import { fetchMsalModuleConfigSynchronously } from './shared/functions/fetchMsalModuleConfigSynchronously';
import { MSAL_CONFIG } from '@azure/msal-angular/dist/msal.service';
import { CoreModule } from './core/core.module';
import { RomeInterceptorService } from './services/interceptors/rome-interceptor.service';
import { Router } from '@angular/router';

export function loadEnvironmentConfigService(configService: AppConfigService): Function {
  return () => { return configService.loadEnvironmentConfig() };
}

@NgModule({
  declarations: [
    AppComponent
    ],
  imports: [
    BrowserModule,
    HttpClientModule,
    BrowserAnimationsModule,
    CoreModule
  ],
  providers: [
    AppConfigService,
    MsalService,
    BroadcastService,
    {
      provide: APP_INITIALIZER,
      useFactory: loadEnvironmentConfigService,
      deps: [AppConfigService],
      multi: true
    },
    {
      provide: MSAL_CONFIG,
      useFactory: () => {
        return fetchMsalModuleConfigSynchronously();
      }
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: RomeInterceptorService,
      deps: [
        MsalService,
        Router
      ],
      multi: true
    }
  ],
  exports: [
  ],
  bootstrap: [AppComponent]
})
export class AppModule {

}
